
let data = {
    pageTitle: '22'
};

// reducer
export function pageTitle (state = data.pageTitle, action) {
    // action
    switch (action.type) {
        case 'SET_PAGE_TITLE':
            return action.data;
        default:
            return state
    }
}

// action
export function setPageTitle (data) {
    return (dispatch, getState) => {
        dispatch({
            type: 'SET_PAGE_TITLE',
            data: data
        })
    }
}



